@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:ital,wght@0,300;0,400;0,500;0,600;1,300;1,400;1,500&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

html,
body,
#root,
.App {
	height: 100vh;
}

.App h1,
.App h2,
.App span,
.App input {
	font-family: 'Merriweather Sans', sans-serif;
}

.admin-page {
	height: 100vh;
	background-color: rgb(0, 58, 13);
}

.admin-page .header {
	background-color: rgb(192, 192, 192);
	border-bottom: 5px solid rgb(87, 87, 87);
	height: 50px;
	padding: 10px 15px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.admin-page .header h1 {
	text-transform: uppercase;
	font-weight: bold;
	font-size: 18px;
}

.admin-page .header button {
	text-transform: uppercase;
	border: none;
	background-color: transparent;
	padding: 0;
	color: rgb(197, 0, 0);
}

.admin-page h2 {
	color: rgb(255, 255, 255);
	font-size: 17px;
	font-weight: bold;
	padding: 30px;
}

.admin-page .admin-user-item {
	cursor: pointer;
	background-color: rgb(170, 170, 170);
	border: 1px solid black;
	width: 50%;
	padding: 10px;
	margin: 10px 30px;
}

.admin-page .admin-user-item:hover {
	background-color: rgb(139, 139, 139);
}

.login {
	background-color: rgb(194, 194, 194);
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.login h1 {
	font-size: 30px;
	margin-bottom: 10px;
	margin-top: 20%;
}

.login input {
	margin: 5px 0;
	border-radius: 3px;
	border: none;
	padding: 5px;
	width: 300px;
	font-size: 14px;
}

.login .enter-button {
	border-radius: 3px;
	padding: 7px;
	border: 1px solid rgb(0, 0, 0);
	background-color: rgb(255, 255, 255);
	margin: 5px 0;
}

.login button:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

.login .not-a-user-button {
	background-color: transparent;
	padding: 0;
	color: rgb(255, 255, 255);
	border: none;
	margin-top: 20px;
}

.login .login-error-span {
	color: rgb(255, 0, 0);
	margin: 10px 0;
	font-size: 14px;
}

.sign-up {
	background-color: rgb(194, 194, 194);
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.sign-up input {
	margin: 5px 0;
	border-radius: 3px;
	border: none;
	padding: 5px;
	width: 300px;
	font-size: 14px;
}

.sign-up .name-input,
.sign-up .sign-up-successful-span {
	margin-top: 18%;
}

.sign-up .sign-up-successful-span {
	margin-bottom: 15px;
	color: rgb(0, 124, 0);
}

.sign-up button {
	border-radius: 3px;
	padding: 7px;
	border: 1px solid rgb(0, 0, 0);
	background-color: rgb(255, 255, 255);
	margin: 5px 0;
}

.sign-up button:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

.sign-up .sign-up-error-span {
	color: rgb(255, 0, 0);
	margin: 10px 0;
	font-size: 14px;
}

.user-detail-page {
	height: 100vh;
	background-color: rgb(196, 196, 196);
	padding: 15px 30px;
}

.user-detail-page .header {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 30px;
}

.user-detail-page .header button {
	background-color: transparent;
	border: none;
	margin-right: 20px;
}

.user-detail-page .header h1 {
	font-size: 20px;
}

.user-detail-page .user-info-section > div {
	margin: 15px 0;
}

.user-detail-page .user-info-section ul {
	margin-left: 10px;
	list-style-type: disc;
	list-style: disc outside none;
}

.user-detail-page .user-info-tasks-section label {
	display: block;
	margin-bottom: 10px;
}

.user-detail-page .user-info-tasks-section li {
	display: list-item;
	margin: 1em;
}

.user-page {
	height: 100vh;
	background-color: rgb(0, 58, 13);
}

.user-page .header {
	background-color: rgb(192, 192, 192);
	border-bottom: 5px solid rgb(87, 87, 87);
	height: 50px;
	padding: 10px 15px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.user-page .header h1 {
	text-transform: uppercase;
	font-weight: bold;
	font-size: 18px;
}

.user-page .header button {
	text-transform: uppercase;
	border: none;
	background-color: transparent;
	padding: 0;
	color: rgb(197, 0, 0);
}

.user-page .tasks-section {
	padding: 30px;
}

.user-page .tasks-section h2 {
	color: rgb(255, 255, 255);
	font-size: 17px;
	font-weight: bold;
}

.user-page .add-task-section {
	margin-top: 15px;
}

.user-page .add-task-section input {
	width: 350px;
	border-radius: 3px;
	border: none;
	padding: 5px;
	font-size: 13px;
}

.user-page .add-task-section button {
	background-color: transparent;
	border: none;
	padding: 0;
	color: rgb(255, 255, 255);
	margin-left: 10px;
}

.user-page .add-task-section button:hover {
	text-decoration: underline;
}

.user-page .add-task-button {
	background-color: transparent;
	border: none;
	margin-top: 15px;
}

.user-page .task {
	background-color: rgb(167, 167, 167);
	border: 1px dotted rgb(255, 255, 255);
	width: 50%;
	padding: 10px;
	margin: 10px 0;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.user-page .task span {
	font-size: 13px;
}

.user-page .task button {
	background-color: transparent;
	border: none;
}

